<template>
  <div>
    <van-nav-bar
      title="新建事项提醒"
      left-text=""
      left-arrow
      @click-left="onClickLeft"
      class="shawdow"
    />
    <div class="logoWrapper">
      <img class="logo" src="../assets/1.gif" />
    </div>
    <van-cell-group class="form">
      <van-field
        v-model="title"
        required
        clearable
        size="large"
        label="推送标题"
        placeholder="请输入标题"
      />

      <van-field
        v-model="content"
        label="推送内容"
        size="large"
        placeholder="请输入内容"
        required
      />
      <!--required-->
      <van-field
        v-model="time"
        label="推送时间"
        size="large"
        @focus="selectTime"
        placeholder="选择时间(若间隔推送,自动获取有效信息)"
      />

      <van-field
        v-model="cron"
        label="cron"
        size="large"
        @blur="verifyCron"
        placeholder="可选(若自定义cron,推送频率自动失效)"
      />
      <van-popup v-model="show" position="bottom">
        <van-datetime-picker
          v-model="currentDate"
          type="datetime"
          @confirm="getTime"
          @cancel="cancel"
          :min-date="minDate"
          :max-date="maxDate"
        />
      </van-popup>
    </van-cell-group>
    <van-dropdown-menu>
      <van-dropdown-item v-model="rate" :options="option1" />
      <van-dropdown-item v-model="value3" :options="option3" />
      <van-dropdown-item v-model="value2" :options="option2" />
    </van-dropdown-menu>

    <div class="submit">
      <van-button
        type="primary"
        @click="newTodo"
        class="btn"
        :block="true"
        size="normal"
        >新建</van-button
      >
    </div>
  </div>
</template>

<script>
import axios from "@/axios";
import mixin from "@/mixins/mixin.js";
import mixinMethod from "@/mixins/mixinMethod.js";
export default {
  mixins: [mixin, mixinMethod],
  name: "NewTodo",
  data() {
    return {
      title: "代办事项提醒",
      content: "",
      originDate: "",
      time: "",
      cron: "",
      minHour: 10,
      maxHour: 20,
      minDate: new Date(),
      maxDate: new Date(2022, 12, 31),
      currentDate: new Date(),
      show: false,
      radio: 1,
      rate: 1,
      option1: [
        { text: "仅提醒一次", value: 1 },
        { text: "每天一次", value: 2 },
        { text: "每周一次", value: 3 },
        { text: "每月一次", value: 4 },
        { text: "每年一次", value: 5 }
        // 自定义cron value:6
      ],
      value2: 1,
      option2: [
        { text: "重要且紧急", value: 1 },
        { text: "重要不紧急", value: 2 },
        { text: "紧急不重要", value: 3 },
        { text: "不紧急不重要", value: 4 }
      ],
      value3: 2,
      option3: [
        { text: "星期日", value: 1 },
        { text: "星期一", value: 2 },
        { text: "星期二", value: 3 },
        { text: "星期三", value: 4 },
        { text: "星期四", value: 5 },
        { text: "星期五", value: 6 },
        { text: "星期六", value: 7 }
      ]
    };
  },
  computed: {
    // dType: function(){
    //   return this.rate==2||this.rate==3?true:false
    // }
  },
  methods: {
    formatter(value) {
      let year, month, day, hour, minute;
      year = value.getFullYear();
      month = value.getMonth() + 1;
      day = value.getDate();
      hour = value.getHours();
      minute = value.getMinutes();
      month = month < 10 ? "0" + month : month;
      day = day < 10 ? "0" + day : day;
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      return (
        year + "-" + month + "-" + day + " " + hour + ":" + minute + ":" + "00"
      );
    },
    getTime(value) {
      let time = this.formatter(value);
      this.time = time;
      // 保存原始Date对象
      this.originDate = value;
      this.show = false;
    },
    newTodo() {
      if (
        this.title == "" ||
        this.content == "" ||
        (this.time == "" && this.cron == "")
      ) {
        this.$toast("必填信息不能为空");
        return;
      }
      // 只推送一次，直接传时间
      if (this.rate === 1 && this.cron === "") {
        axios({
          method: "post",
          url: "/api/todo_job",
          data: {
            title: this.title,
            content: this.content,
            pushTime: this.time,
            frequency: this.rate,
            type: this.value2
          }
        }).then(res => {
          if (res.data.code === 200) {
            this.content = "";
            this.time = "";

            this.$toast("新建成功");
          }
        });
      }
      // 间隔推送
      else {
        let cron = null;
        // 按要求拼接cron
        if (this.cron === "") {
          let min = this.originDate.getMinutes();
          let hour = this.originDate.getHours();
          let day = this.originDate.getDate();
          let mon = this.originDate.getMonth();
          this.originDate.getHours();
          switch (this.rate) {
            case 2:
              cron = "0 " + min + " " + hour + " * * ?";
              break;
            case 3:
              cron = "0 " + min + " " + hour + " ? * " + this.value3;
              break;
            case 4:
              cron = "0 " + min + " " + hour + " " + day + " * ?";
              break;
            case 5:
              cron = "0 " + min + " " + hour + " " + day + " " + mon + " ?";
              break;
          }

          axios({
            method: "post",
            url: "/api/todo_job",
            data: {
              title: this.title,
              content: this.content,
              frequency: this.rate,
              cron: cron,
              type: this.value2
            }
          }).then(res => {
            this.content = "";
            this.time = "";
            this.cron = "";
            if (res.data.code === 200) {
              this.$toast("新建成功");
            }
          });
        }
        // 自定义cron
        else {
          axios({
            method: "post",
            url: "/api/todo_job",
            data: {
              title: this.title,
              content: this.content,
              cron: this.cron,
              frequency: 6,
              type: this.value2
            }
          }).then(res => {
            if (res.data.code === 200) {
              this.content = "";
              this.time = "";
              this.cron = "";
              this.$toast("新建成功");
            }
          });
        }
      }
    }
  }
};
</script>

<style lang="stylus" scoped>
.radi
    padding 5px
</style>
