// 时间模拟框的通用方法
import axios from "@/axios";
const mixinMethod = {
  methods: {
    selectTime() {
      this.show = true;
    },

    cancel() {
      this.show = false;
    },
    verifyCron() {
      axios({
        method: "get",
        url: "/verify_cron",
        params: {
          cron: this.cron
        }
      }).then(res => {
        if (res.data.code === 200) {
          this.$toast("cron表达式验证成功");
        }
      });
    }
  }
};
export default mixinMethod;
